import React from 'react';
import ReactDOM from 'react-dom';
import './bootstrap.min.css';
import './styles.css';

// Imported necessary React-Bootstrap elements
import Table from 'react-bootstrap/Table';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';


class Etooltest extends React.Component {
	constructor(props) {
		super(props);
		this.defaultOptions = ["Apple", "Banana", "Strawberry", "Pear", "Orange"];

		this.state = {
			keyword : '',
			item : this.defaultOptions[0],
			suggestions : [],
		}

		// Contains a list of keyword/item combinations
		this.keywordItemRecord = {};

		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.generateOptions = this.generateOptions.bind(this);
	}

	handleChange(event){
		this.setState({ suggestions : [] });
		this.setState({ [event.target.name] : event.target.value }, () => {
				console.log(unsorted);
				if(this.keywordItemRecord[this.state.keyword]) {
					var unsorted = this.keywordItemRecord[this.state.keyword];
					//console.log(unsorted);
					if(this.keywordItemRecord[this.state.keyword][this.state.item]) {
						//console.log(this.keywordItemRecord[this.state.keyword][this.state.item]);
						console.log(unsorted);
						var array = [];
						array.push(unsorted);
						this.setState({ suggestions : array });
					}
				}
			}
		)



	}

	handleSubmit(event){
		if(!this.keywordItemRecord[this.state.keyword])
		{
			this.keywordItemRecord[this.state.keyword] = {};
			this.keywordItemRecord[this.state.keyword][this.state.item] = 1;
		}
		else {
			if(!this.keywordItemRecord[this.state.keyword][this.state.item]) {
				this.keywordItemRecord[this.state.keyword][this.state.item] = 1;
			}
			else {
				this.keywordItemRecord[this.state.keyword][this.state.item] = this.keywordItemRecord[this.state.keyword][this.state.item] + 1 ;
			}
		}
		event.preventDefault();
	}


	generateOptions()
	{
		var options = [];
		var suggestedOptions = [];

		//console.log(this.state.suggestions);
		if(this.state.suggestions.length > 0){
			options.push(<option disabled>Suggestions</option>);
			var keys = Object.keys(this.state.suggestions[0]);
			for(var i = 0; i < keys.length; i++)
			{
					console.log(keys[i]);
					options.push(<option value={keys[i]}>{keys[i]}</option>)
					suggestedOptions.push(keys[i]);
			}
			options.push(<option disabled>Other Options:</option>);
		}

		console.log(this.defaultOptions);

		for(var j = 0; j < this.defaultOptions.length; j++)
		{
			if(!suggestedOptions.includes(this.defaultOptions[j]))
			{
				options.push(<option value={this.defaultOptions[j]}>{this.defaultOptions[j]}</option>);
			}
		}

		return options;

	}

	render () {


		return (
			<div className="container">
			<form onSubmit={this.handleSubmit}>
				<label>Enter Keyword:
					<div><input type="text" name="keyword" value={this.state.keyword} onChange={this.handleChange} /></div>
					</label>
			<div><select name="item" value={this.state.item} onChange={this.handleChange}>
				{this.generateOptions()}
			</select></div>
			<div>	<button type="submit" >Submit</button></div>
			</form>

			</div>
		);
	}
}


// =====================================================================================

// Root rendering component attached to it first.
ReactDOM.render(
  <Etooltest />,
  document.getElementById('root')
);
